import { Button, TextField, Alert } from '@mui/material';
import React, { useState } from 'react';
import useAuth from '../../../hooks/useAuth';

const MakeAdmin = () => {
    const [email, setEmail] = useState('');
    const [success, setSuccess] = useState(false);
    const { token } = useAuth();
    const handleOnBlur = e => {
        setEmail(e.target.value);
    }
    const handleAdminSubmit = e => {
        const user = { email };
        fetch('https://doctor-portal-server-site.herokuapp.com/users/admin', {
            method: 'PUT',
            headers: {
                'authorization': `Bearer ${token}`,
                'content-type': 'application/json'
            },
            body: JSON.stringify(user)
        })
        .then(res => res.json())
        .then(data => {
            if(data.modifiedCount){
                console.log(data);
                setSuccess(true)
            }
            
        })

        e.preventDefault()
    }
    return (
        <div>
            <h2>Make An Admin</h2>
            <form onSubmit={handleAdminSubmit}>
                <TextField 
                sx={{ width:'50%', margin: '10px'}}
                label="Email"
                type="email"
                onBlur={handleOnBlur}
                variant="outlined" />
                <Button sx={{ mt: "20px"}} type="submit" variant="contained">Make Admin</Button>
            </form>
            {success && <Alert severity="success">Admin Added successfully!</Alert>}  
        </div>
    );
};

export default MakeAdmin;